:root {
  --first-acent-color: #082911;
  --second-acent-color: #decea9;
  --just-white: #fff;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
}

.search-form {
  width: 100%;
  height: 40px;
  background-color: #ddd06e;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  box-shadow: 0 10px 10px -5px rgba(0, 0, 0, .5);
}

.load-more {
  cursor: pointer;
  color: var(--just-white);
  background-color: #ddd06e;
  border: 1px solid rgba(255, 255, 255, .3);
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin: 0 auto 10px;
  padding: 10px 35px;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  justify-content: space-around;
  gap: 15px;
  padding-top: 50px;
  padding-left: 25px;
  padding-right: 25px;
  display: flex;
}

.photo-card {
  flex-direction: column;
  display: flex;
  box-shadow: 0 10px 10px -5px rgba(0, 0, 0, .3);
}

.photo-card-img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.info {
  justify-content: space-around;
  display: flex;
}

.info-item {
  text-align: center;
}

.info-item p {
  color: #4e4b4b;
  margin: 10px 0;
}

